.App {
  position: relative;
  color: #111;
  font-family: "Apple SD Gothic Neo", sans-serif;
}
.App-parcoords {
  position: absolute;
  width: 1000px;
  margin: 0 auto;
  top: calc(50vh - 300px);
  left: calc(50% - 500px);
}

.title {
  position: fixed;
  font-size: 28px;
  font-weight: bolder;
  text-align: center;
  width: 100%;
}

.title > div {
  display: inline-block;
}

.title span {
  -webkit-text-stroke: 1px #111;
  text-stroke: 1px #111;
  color: #fff1e5;
}
.title.top {
  top: 1vh;
}

.title.bottom {
  bottom: 1vh;
}



