.Tick {
  pointer-events: none;
}

.Tick.selectable {
  pointer-events: all;
  cursor: pointer;
}

.Tick .Tick-text {
  -webkit-transition: stroke-width .8s, fill .8s, stroke-opacity .8s, fill-opacity .8s;
  -o-transition: stroke-width .8s, fill .8s, stroke-opacity .8s, fill-opacity .8s;
  transition: stroke-width .8s, fill .8s, stroke-opacity .8s, fill-opacity .8s;
  fill: #111;
  stroke: #111;
  stroke-width: 0;
  stroke-opacity: 0;
  font-size: 18px;
  font-weight: bold;
  shape-rendering: crispEdges;
}

.Tick .Tick-text.stroke {
  fill: #fff1e5;
  stroke-opacity: 1;
  stroke-width: 1px;
}

.Tick.selected .Tick-text {
  fill-opacity: 0;
}

.Tick.deselected .Tick-text {
  fill-opacity: 0.2;
  stroke-opacity: 0.2;
}


