.Series {
  -webkit-transition: opacity .8s;
  -o-transition: opacity .8s;
  transition: opacity .8s;
}

.Series .linePath {
  -webkit-transition: opacity .8s;
  -o-transition: opacity .8s;
  transition: opacity .8s;
  fill: none;
  stroke: #444;
  stroke-width: 1px;
  stroke-dasharray: 15, 10, 5, 10;
}

.Series.deselected .linePath, .Series.selected .linePath {
  opacity: .2;
}

.Series .textPath {
  opacity: 0;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
  font-size: 14px;
}

.Series.selected .textPath {
  opacity: 1;
}

.Series.selected .textPath .bold {
  font-weight: bold;
}

.textPath.hidden {
  visibility: hidden;
}


